.navbar {
  grid-area: n;
  background-color: aliceblue;
  letter-spacing: 0.3rem;
  text-transform: uppercase;
  padding: 0.6rem;
  line-height: 2rem;
  color: rgb(30, 48, 62);
}

.item {
  width: 3fr;
  height: 1px;
  margin-left: 0;
  margin-right: 0;
  text-align: left;
  background: linear-gradient(
    to left,
    rgb(18, 101, 117),
    rgb(18, 117, 89),
    rgb(18, 101, 117)
  );
}

a {
  text-decoration: none;
  text-decoration-color: none;
  color: black;
  width: 100%;
  height: 100%;
}

a.active {
  /* -moz-animation: neon1 1.5s ease-in-out infinite alternate;
  animation: neon1 1.5s ease-in-out infinite alternate; */
  text-shadow: 0 0 5px rgb(255, 255, 255), 0 0 15px rgb(35, 228, 160),
    0 0 30px rgb(69, 165, 250), 0 0 20px rgb(9, 250, 182),
    0 0 35px rgb(29, 191, 145), 0 0 40px rgb(2, 213, 255),
    0 0 50px rgb(27, 214, 252), 0 0 75px rgb(0, 213, 255);
}

a:hover {
  /* -moz-animation: neon1 1.5s ease-in-out infinite alternate;
  animation: neon1 1.5s ease-in-out infinite alternate; */
  text-shadow: 0 0 5px rgb(255, 255, 255), 0 0 10px rgb(35, 228, 160),
    0 0 15px rgb(69, 165, 250), 0 0 20px rgb(9, 250, 182),
    0 0 35px rgb(29, 191, 145), 0 0 40px rgb(2, 213, 255),
    0 0 50px rgb(27, 214, 252), 0 0 75px rgb(0, 213, 255);
}
